<template>
  <div>
    <el-button v-show="isAdmin" @click="dialogFormVisible = true"></el-button>
    <el-table :data="users" stripe>
      <el-table-column prop="id" label="id" > </el-table-column>
      <el-table-column prop="username" label="" >
      </el-table-column>
      <el-table-column prop="user_type" label=""> </el-table-column>
      <el-table-column prop="email" label=""> </el-table-column>
      
      <el-table-column fixed="right" label="" >
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="small"
            @click="intoEditProject(scope.row)"
            ></el-button
          >
          <el-button
            v-show="isAdmin"
            type="danger"
            size="small"
            @click="deleteUser(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog
      title=""
      :modal-append-to-body="false"
      :visible.sync="dialogFormVisible"
      width="40%"
    >
      <el-form :model="form" label-position="right" label-width="120px">
        <el-form-item label="" style="margin-top: 20px">
          <el-input  placeholder="" v-model="form.username" style="width: 350px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" v-model="form.email" style="width: 350px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select size="small" v-model="form.is_superuser" placeholder="">
            <el-option
              v-for="userType in userTypeList"
              :key="userType.num"
              :label="userType.type"
              :value="userType.num"
            >
            </el-option>
          </el-select>
        </el-form-item>
        
        <el-form-item label="">
          <el-input v-model="form.password" style="width: 350px" placeholder="" show-password> </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addUser"> </el-button>
        <el-button type="danger" @click="cancleAdd"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="isEdit"
      :modal-append-to-body="false"
      width="30%"
    >
      <el-form :model="edit_form" label-position="right" label-width="120px">
        <el-form-item label="id" style="margin-top: 20px; display: none">
          <el-input v-model="edit_form.id" style="width: 350px"></el-input>
        </el-form-item>
        <el-form-item label="" style="margin-top: 20px">
          <el-input disabled v-model="edit_form.username" style="width: 350px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="edit_form.email" style="width: 350px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input 
            disabled
            v-model="edit_form.user_type"
            style="width: 350px"
          ></el-input>
        </el-form-item>
        
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="editUser"> </el-button>
        <el-button type="danger" @click="isEdit = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
axios.defaults.withCredentials = true;
export default {
  name: "Project",
  data() {
    return {
      users: [],
      isEdit: false,
      userTypeList: [{"num":1,"type":""},{"num":2,"type":""}],
      dialogFormVisible: false,
      token: localStorage.Authorization,
      currentUser: localStorage.UserId,
      form: {
        username: "",
        email: "",
        password: "",
        is_superuser: "",
      },
      edit_form: {
        id: "",
        username: "",
        email: "",
        is_superuser: "",
      },
      
    };
  },
  methods: {
    addUser() {
      const addProjectData = {...this.addProjectData,...this.form}
      addProjectData['is_superuser'] = this.form['is_superuser'] === 0
      axios
        .post("http://127.0.0.1:8001/api/user/", addProjectData, {
          headers: {
            Authorization: this.token,
          },
          responseType: "json",
        })
        .then((response) => {
          if (response.data.code === 1) {
            this.dialogFormVisible = false;
            this.getUserList();
          } else {
            this.$message(response.data.msg);
          }
          this.resetForm();
        })
        .catch((err) => {
          this.$message("");
        });
    },
    getUserList() {
      axios
        .get("http://127.0.0.1:8001/api/user/", {
          headers: {
            Authorization: this.token,
          },
          responseType: "json",
        })
        .then((response) => {
          response.data.data.forEach(user => {
            if(user['is_superuser']){
              user['user_type'] = ''
            }else{
              user['user_type'] = ''
            }
          })
          this.users = response.data.data;

        });
    },
    cancleAdd() {
      this.resetForm();
      this.dialogFormVisible = false;
    },
    // 
    intoEditProject(user) {
      
      this.edit_form = { ...this.edit_form, ...user };
      //   
      this.edit_form['user_type'] = this.edit_form['is_superuser'] ? "" : ""
      this.isEdit = true;
    },
    editUser() {
      axios
        .put(
          "http://127.0.0.1:8001/api/user/" + this.edit_form.id + "/",
          this.edit_form,
          {
            headers: {
              Authorization: this.token,
            },
            responseType: "json",
          }
        )
        .then((response) => {
          if (response.data.code == 1) {
            this.isEdit = false;
            this.getUserList();
          } else {
            this.$message(response.data.msg);
          }
        })
        .catch((err) => {
          this.$message("");
        });
    },
    deleteUser(user) {
      const isDel = confirm("");
      if (isDel) {
        axios
          .delete("http://127.0.0.1:8001/api/user/" + user.id + "/",{
            headers: {
              Authorization: this.token,
            },
            responseType: "json",
            
          })
          .then((response) => {
            this.$message(response.data.msg);
            if (response.data.code == 1) {
              this.getUserList();
            }
          });
      }
    },
    resetForm() {
      this.form.username = "";
      this.form.email="";
      this.form.password = ""
      this.form.is_superuser= "";
    },
  },
  computed:{
    isAdmin(){
      return this.currentUser == 1
    }
  },
  mounted() {
    this.getUserList();
  },
};
</script>

