<template>
  <div>
    <div>
      
      <el-select size="small" v-model="pro_id" placeholder="">
        <el-option
          v-for="pro in authorProjectList"
          :key="pro.id"
          :label="pro.name"
          :value="pro.id"
          :project_index="pro.index"
        >
        </el-option>
      </el-select>

      <el-button
        size="small"
        style="margin-left: 10px"
        @click="concurrentExcution"
        ></el-button
      >
      <el-button
        size="small"
        style="margin-left: 10px"
        @click="lookReportSummary"
        ></el-button
      >
      <el-button
        size="small"
        style="margin-left: 10px"
        @click="dialogFormVisible = true"
        ></el-button
      >
      <el-button
        size="small"
        style="margin-left: 10px"
        @click="uploadUtils"
        >
        
        </el-button>
      <el-button
        size="small"
        style="margin-left: 10px"
        @click="intoSetAutoTask"
        >
        
        </el-button>

      <div style="float: right">
        
        <el-select size="small" v-model="excuseHost" placeholder="">
          <el-option
            v-for="host in currentHostList"
            :key="host.index"
            :label="host"
            :value="host"
          >
          </el-option>
        </el-select>
      </div>
    </div>

    <el-table :data="cases" stripe>
      <el-table-column prop="id" label="id" > </el-table-column>
      <el-table-column prop="name" label="" >
      </el-table-column>
      <el-table-column prop="script_name" label=""> </el-table-column>
      <el-table-column prop="is_threads" label="" ></el-table-column>
      <el-table-column prop="is_auto_excuse" label="" ></el-table-column>
      <el-table-column fixed="right" label="" >
        <template slot-scope="scope">
          <el-button
            ref="excuseBtn"
            type="primary"
            size="small"
            @click="excuseCase(scope.row)"
            ></el-button
          >
          <el-button
            ref="excuseBtn"
            type="danger"
            size="small"
            @click="intoEditCase(scope.row)"
            ></el-button
          >
          <el-button
            ref="excuseBtn"
            type="danger"
            size="small"
            @click="lookReport(scope.row)"
            ></el-button
          >
          <el-button type="danger" size="small" @click="deleteCase(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog
      title=""
      :modal-append-to-body="false"
      :visible.sync="dialogFormVisible"
      width="30%"
    >
      <el-form :model="form" label-position="right" label-width="120px">
        <el-form-item label="" style="margin-top: 20px">
          <el-input v-model="form.name" style="width: 350px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.retry_count" style="width: 350px"> </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="form.is_thread" label="1"></el-radio>
          <el-radio v-model="form.is_thread" label="2"></el-radio>
        </el-form-item>
        <el-form-item label="">
            <el-radio v-model="form.is_auto_excuse" label="1"></el-radio>
            <el-radio v-model="form.is_auto_excuse" label="0"></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            ref="uploadFile"
            class="upload-demo"
            :action="upLoadUrl"
            :on-success="handleSuccess"
            :before-remove="beforeRemove"
            :before-upload="beforeUpload"
            multiple
            :auto-upload="false"
            :limit="1"
            :on-exceed="handleExceed"
            :file-list="fileList"
            :headers="myHeader"
          >
            <el-button
              slot="trigger"
              size="small"
              type="success"
              @click="submitUpload"
              ></el-button
            >
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addCase"> </el-button>
        <el-button type="danger" @click="cancleAddCase"
          > </el-button
        >
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="isEdit"
      :modal-append-to-body="false"
      width="30%"
    >
      <el-form :model="edit_form" label-position="right" label-width="120px">
        <el-form-item label="" style="margin-top: 20px">
          <el-input v-model="edit_form.name" style="width: 350px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="edit_form.retry_count" style="width: 350px">
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="edit_form.is_thread" label="1"></el-radio>
          <el-radio v-model="edit_form.is_thread" label="2"></el-radio>
        </el-form-item>
        <el-form-item label="">
            <el-radio v-model="edit_form.is_auto_excuse" label="1"></el-radio>
            <el-radio v-model="edit_form.is_auto_excuse" label="0"></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            ref="uploadFile"
            class="upload-demo"
            :action="upLoadUrl"
            :on-success="handleSuccess"
            :before-remove="beforeRemove"
            :before-upload="beforeUploadEdit"
            multiple
            :auto-upload="false"
            :limit="1"
            :on-exceed="handleExceed"
            :file-list="utilsFileList"
            :headers="myHeader"
          >
            <el-button slot="trigger" size="small" type="success"
              ></el-button
            >
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="editCase"> </el-button>
        <el-button type="danger" @click="isEdit = false"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :modal-append-to-body="false"
      :visible.sync="isLookReport"
      width="80%"
    >
      <div>
        <div ref="reportHTML" v-html="htmlText" class="web-con"></div>
      </div>
      
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :modal-append-to-body="false"
      :visible.sync="isLookReportSummary"
      width="30%"
    >
      <div style="text-align:center;font-size:16px" ref="reportSummary"></div>
      
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :modal-append-to-body="false"
      :visible.sync="clientUpdate"
      width="60%"
    >
      <div>
        <div>
          <h4>:,;</h4>
          <el-button
            size="small"
            style="margin-left: 10px"
            type="success"
            ><span class="link-url" @click="downloadClient"></span></el-button
          >
        </div>
        <div>
          <h4>:public,python</h4>
          <el-upload
            class="upload-demo"
            :action="uploadUtilsUrl"
            multiple
            :auto-upload="true"
            :limit="3"
            :before-upload="beforeUtilUpload"
            :file-list="utilsFileList"
            :headers="myHeader"
          >
            <el-button slot="trigger" size="small" type="success" style="margin-left: 10px"
              ></el-button
            >
      </el-upload>
        </div>
      </div>
      
    </el-dialog>


    <!--  -->
    <el-dialog
      title=""
      :modal-append-to-body="false"
      :visible.sync="isSetAuto"
      width="50%"
    >

    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="" name="first">
        <el-form :model="autoTask" label-position="right" label-width="120px">
          <el-form-item label="" style="margin-top: 20px">
            <el-switch
              v-model="autoTask.autoSwitch"
              active-color="#13ce66"
              inactive-color="#ff4949">
            </el-switch>
          </el-form-item>
          
          <el-form-item label=" ">
            <el-time-select
              v-model="autoTask.autoExcuseTime"
              :picker-options="{
                start: '00:00',
                step: '00:15',
                end: '23:59'
              }"
              placeholder="">
            </el-time-select>
          </el-form-item>
          
        </el-form>
        <div align='center'>
          <el-button type="primary" @click="openOrCloseTask"> </el-button>
          <el-button type="danger" @click="cancleSetAuto"> </el-button>
        </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <h4></h4>
          <div>
            : 
            <el-select size="small" v-model="sendType" placeholder="">
              <el-option
                v-for="item in typeList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            &nbsp&nbsp
            <span v-show="sendType==1">: 
              <el-select size="small" v-model="sendUserList" multiple placeholder="">
                <el-option
                  v-for="u in users"
                  :key="u.id"
                  :label="u.username"
                  :value="u.id"
                >
                </el-option>
              </el-select>
            </span>

            <span v-show="sendType==2">webhook: 
              <el-input v-model="webhook" size="small" style="width: 250px"></el-input>
            </span>
            &nbsp&nbsp
            <el-button @click="addNotice"  icon="el-icon-check" circle size='small'></el-button>
          </div>
          
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import constant from "../constant/constant"
import {mapState,mapMutations} from 'vuex';
axios.defaults.withCredentials = true;
export default {
  name: "Project",
  data() {
    return {
      // 
      activeName: 'first',
      sendType: '1',
      typeList:[{value:'1',label:''},{value:'2',label:''}],
      // 
      cases: [],
      // 
      sendUserList:[],
      // webhook
      webhook:'',
      // 
      isEdit: false,
      // 
      dialogFormVisible: false,
      // 
      isSetAuto: false,
      autoTask:{
        // 
        autoSwitch: false,
        // 
        autoExcuseTime: ''
      },
      // 
      autoTaskStatus:{
        // 
        autoSwitch: false,
        // 
        autoExcuseTime: ''
      },
      // id
      pro_id: 0,
      form: {
        name: "",
        is_thread: "1",
        retry_count: "",
        script_name: "",
        author: localStorage.getItem("UserId"),
        is_auto_excuse: "1"
      },
      edit_form: {
        name: "",
        is_thread: "",
        retry_count: "",
        script_name: "",
        author: localStorage.getItem("UserId"),
        is_auto_excuse: ""
      },
      // 
      authorProjectList: [],
      // 
      fileList: [],
      // 
      utilsFileList: [],
      // 
      fileName: "",
      token: localStorage.Authorization,
      myHeader: { Authorization: localStorage.Authorization },
      // 
      hostList: {},
      // 
      currentHostList: [],
      // 
      excuseHost: "",
      // 
      htmlText: "",
      // 
      isLookReport: false,
      // 
      isLookReportSummary: false,
      // 
      reportSummaryContent: '',
      // 
      clientUpdate: false
    };
  },
  methods: {
    handleClick(tab, event) {
        
    },
    handleSuccess(file) {
      this.fileList.splice(0, 1);
    },
    beforeUtilUpload(file) {
      const isPY = file.type === 'text/x-python';
      const isLt200k = file.size / 1024 / 1024 < 0.2;

      if (!isPY) {
        this.$message.error('python!');
      }
      if (!isLt200k) {
        this.$message.error(' 200KB!');
      }
      return isPY && isLt200k;
    },
    beforeUpload(file) {
      this.form.script_name = file.name;
    },
    beforeUploadEdit(file) {
      this.edit_form.script_name = file.name;
    },
    handleExceed() {
      this.$message.warning(``);
    },
    beforeRemove(file, fileList) {
      const isdel = this.$confirm(` ${file.name}`);
      if (isdel) this.form.script_name = "";
      return isdel;
    },
    // 
    submitUpload() {
      this.$refs.uploadFile.submit();
    },
    // 
    addCase() {
      let addCaseData = this.form;
      addCaseData["project_id"] = this.pro_id;
      // 
      this.submitUpload();
      axios
      .post(constant.baseURL+"case/", addCaseData, {
        headers: {
          Authorization: this.token,
        },
        responseType: "json",
      })
      .then((response) => {
        this.dialogFormVisible = false;
        this.getCaseList(this.pro_id);
        this.resetForm();
      });
    },
    // 
    cancleAddCase(){
      this.dialogFormVisible = false
      this.resetForm();
    },
    // 
    getCaseList(pro_id) {
      axios
        .get(constant.baseURL+"case/", {
          headers: {
            Authorization: this.token,
          },
          responseType: "json",
          params: {
            pro_id: pro_id,
          },
        })
        .then((response) => {
          // 
          response.data.data.forEach((c) => {
            c["is_thread"] === 1
              ? (c["is_threads"] = "")
              : (c["is_threads"] = "");
            c["is_auto_excuse"] === 1
            ? (c["is_auto_excuse"] = "")
            : (c["is_auto_excuse"] = "");
          });
          this.cases = response.data.data;
        });
    },
    // 
    intoEditCase(edit_case) {
      // edit_casecase
      if (edit_case.script_name !== "") {
        this.fileList.splice(0, 1, { name: edit_case.script_name });
      }
      if (edit_case.is_threads === "") {
        edit_case["is_thread"] = "1";
      } else {
        edit_case["is_thread"] = "2";
      }
      if (edit_case.is_auto_excuse === "") {
        edit_case["is_auto_excuse"] = "1";
      } else {
        edit_case["is_auto_excuse"] = "0";
      }
      this.edit_form = { ...this.edit_form, ...edit_case };
      delete this.edit_form.is_threads;
      this.isEdit = true;
    },
    // 
    editCase() {
      this.submitUpload();
      axios
        .put(
          constant.baseURL+"case/" + this.edit_form.id + "/",
          this.edit_form,
          {
            headers: {
              Authorization: this.token,
            },
            responseType: "json",
          }
        )
        .then((response) => {
          if (response.data.code == 1) {
            this.isEdit = false;
            this.getCaseList(this.pro_id);
          } else {
            alert(response.data.msg);
          }
        });
    },
    // 
    excuseCase(excuse_case) {
      axios
        .get(constant.baseURL+"case/excuse/" + excuse_case.id + "/", {
          headers: {
            Authorization: this.token,
          },
          responseType: "json",
          params: {
            host: this.excuseHost,
          },
        })
        .then((response) => {
          this.$message(response.data.msg);
        });
    },
    // 
    deleteCase(del_case) {
      const isDel = confirm("");
      if (isDel) {
        axios
          .delete(constant.baseURL+"case/" + del_case.id + "/", {
            headers: {
              Authorization: this.token,
            },
            responseType: "json",
          })
          .then((response) => {
            this.$message(response.data.msg);
            if (response.data.code == 1) {
              this.getCaseList(this.pro_id);
            }
          });
      }
    },
    // 
    lookReport(report_case){
      axios.get(constant.baseURL+"case/report/" + report_case.id + "/",{
            headers: {
              Authorization: this.token,
            },
          })
          .then((response) => {

            if (response.data.code == 3) {
              this.$message(response.data.msg);
            }else{
              // v-htmljs  js 
              this.htmlText = response.data
              let JSScript = ''
              // js
              response.data.replace(/<script.*?>([\s\S]+?)<\/script>/img,function(_,js){    //script
                      JSScript = js
              })
              this.$nextTick(() => {
                // js
                var newElement = document.createElement("script")
                newElement.innerHTML = JSScript
                this.$refs.reportHTML.append(newElement)
              })
              this.isLookReport = true
            }
          });
    },
    // 
    uploadUtils(){
      this.utilsFileList.splice(0,3)
      this.clientUpdate =true
    },

    // 
    resetForm() {
      this.form.name = "";
      this.form.is_thread = "1";
      this.form.is_auto_excuse = "1";
      this.form.retry_count = "";
      this.form.script_name = "";
    },
    // 
    concurrentExcution(){
      axios.get(constant.baseURL+"case/concurrent/",{
        params:{
          project_id: this.pro_id,
          host: this.excuseHost,
        },
        headers: {
          Authorization: this.token,
        },
        responseType: "json",
      }).then(response => {
        confirm(response.data.msg)
      })
    },
    // 
    lookReportSummary(){
      axios.get(constant.baseURL+"case/reportsummary/" +this.pro_id+"/",{
        headers: {
          Authorization: this.token,
        },
        responseType: "json",
      }).then(response => {
        // alert(response.data.data)
        if(response.data.code === 1){
          this.isLookReportSummary = true
          this.$nextTick(() => {
            // js
            this.$refs.reportSummary.innerHTML = response.data.data
          })
          
        }else{
          this.$message(response.data.msg)
        }
      })
    },
    // 
    downloadClient(){
      window.open(constant.baseURL+"case/downloadclient/45/")
    },
    //
    intoSetAutoTask(){
      // 
      axios.get(constant.baseURL+"project/"+this.pro_id+"/",{
        headers: {
          Authorization: this.token,
        },
        responseType: "json",
      }).then(response=>{
        const res_data = response.data.data
        this.autoTask.autoSwitch = res_data['is_auto'] == 1 ? true : false
        this.autoTask.autoExcuseTime = res_data['excuse_time']
      })
      // 
      axios.get(constant.baseURL+"notice/"+this.pro_id+"/",{
        headers: {
          Authorization: this.token,
        },
        responseType: "json",
      }).then(response=>{
        const res_data = response.data.data
        this.sendType = res_data['notice_type']+''
        this.sendUserList.splice(0)
        if(this.sendType === '1'){
          res_data['user_list'].split(',').forEach(user => {
            this.sendUserList.push(Number(user))
          })
        }
        this.webhook = res_data['webhook']

      })

      // 
      this.getUserList('all')
      this.autoTaskStatus = {...this.autoTaskStatus,...this.autoTask}
      this.isSetAuto = true
    },
    cancleSetAuto(){
      this.autoTask = {...this.autoTask,...this.autoTaskStatus}
      this.isSetAuto = false
    },
    // 
    openOrCloseTask(){
      if(this.autoTask.autoSwitch){
        axios.get(constant.baseURL+"case/openMonitor/" +this.pro_id+"/",{
          headers: {
            Authorization: this.token,
          },
          responseType: "json",
          params: {
            excuse_time: this.autoTask.autoExcuseTime,
          },
        }).then(response => {
          // alert(response.data.data)
          this.isSetAuto = false
          this.$message(response.data.msg)
        })
      }else{
        axios.delete(constant.baseURL+"case/openMonitor/" +this.pro_id+"/",{
          headers: {
            Authorization: this.token,
          },
          responseType: "json",
        }).then(response => {
          this.isSetAuto = false
          this.$message(response.data.msg)
        })
      }
    },
    ...mapMutations(['getUserList']),
    addNotice(){
      // 
      let requestData ={
        project_id:this.pro_id,
      }
      if(this.sendType == '1'){
        const len = this.sendUserList.length
        if(len === 0){
          this.$message.error("")
          return
        }else{
          requestData['notice_type'] = 1
          let resUsers = this.sendUserList[0]
          for(var i=1;i<len;i++){
            resUsers += ','+this.sendUserList[i]
          }
          requestData['user_list'] = resUsers
        }
        
      }else{
        requestData['notice_type'] = 2
        requestData['webhook'] = this.webhook
      }
      // 
      axios.post(constant.baseURL+"notice/",requestData,{
        headers: {
            Authorization: this.token,
        },
        responseType: "json",
      }).then(res=>{
        this.$message(res.data.msg)
        this.isSetAuto = false
      })
    }
    
  },
  computed: {
    // 
    upLoadUrl() {
      return constant.baseURL+"case/uploadScript/" + this.pro_id + "/";
    },
    uploadUtilsUrl(){
      return constant.baseURL+"case/uploadUtils/" + this.pro_id + "/";
    },
    ...mapState(['users'])
  },
  watch: {
    pro_id(newValue, oldValue) {
      this.getCaseList(newValue);
      this.currentHostList = this.hostList[newValue.toString()];
      this.excuseHost = this.currentHostList[0];
    },
  },
  mounted() {
    // 
    axios
      .get(constant.baseURL+"project/", {
        headers: {
          Authorization: this.token,
        },
        responseType: "json",
        params: {
          user_id: localStorage.getItem("UserId"),
        },
      })
      .then((response) => {
        response.data.data.res_pro_list.forEach((pro) => {
          this.authorProjectList.push(pro);
        });
        this.hostList = response.data.data.res_host_list;
        if (response.data.data.res_pro_list.length > 0) {
          this.pro_id = response.data.data.res_pro_list[0].id;
          this.getCaseList(this.pro_id);
        } else {
          this.pro_id = "";
        }
      });
  },
};
</script>

<style>
</style>